
:root{
    --rainbow-1: #da66b7;
    --rainbow-2: #7549f0;
    --rainbow-3: #4989c6;
    --rainbow-4: #72bf8d;
    --rainbow-5: #f4c950;
}

/* colors */
.circles {
    display: flex;
    gap: 1rem;
}
.circle {
    height: 40px;
    width: 40px;
    min-width: 40px;
    min-height: 40px;
    margin: 0.5rem 0;
    border-radius: 50%;
}
.desktop-only {
    display: none !important;
}

@media screen and (min-width: 700px) {
    .desktop-only {
        display: flex !important;
    }
}

@media screen and (min-width: 700px) {
    .circle {
        height: 40px;
        width: 40px;
        min-width: 40px;
        min-height: 40px;
    }
}


.rainbow-1 {
    background: var(--rainbow-1);
}

.rainbow-2 {
    background: var(--rainbow-2);
}

.rainbow-3 {
    background: var(--rainbow-3);
}

.rainbow-4 {
    background: var(--rainbow-4);
}

.rainbow-5 {
    background: var(--rainbow-5);
}