
.swiper {
    width: 300px;
    /*width: 80%;*/
    height: 360px;
}

@media (max-width: 900px) {
    .swiper{
        width: 240px;
        height: 320px;
    }
}

.swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    font-size: 22px;
    font-weight: bold;
    color: #fff;
}

.swiper-slide:nth-child(1n) {
    background-color: rgb(206, 17, 17);
    background-image: url('https://image-hub.imgix.net/minesweeper.png?fit=scale&w=300&h=360');
    background-clip: content-box;
    background-position-x: center;
    /*background-size: 100px;*/
    border-width: 10px;
    border-color: rgb(206, 17, 17);

}

.swiper-slide:nth-child(2n) {
    background-color: rgb(0, 140, 255);
    background-image: url('https://image-hub.imgix.net/blog.png?fit=scale&w=300&h=260');
    border-width: 10px;
    border-color: rgb(0, 140, 255);
}

.swiper-slide:nth-child(3n) {
    background-color: rgb(10, 184, 111);
    border-width: 10px;
    border-color: rgb(10, 184, 111);
}

.swiper-slide:nth-child(4n) {
    background-color: rgb(211, 122, 7);
}

.swiper-slide:nth-child(5n) {
    background-color: rgb(118, 163, 12);
}

.swiper-slide:nth-child(6n) {
    background-color: rgb(180, 10, 47);
}

.swiper-slide:nth-child(7n) {
    background-color: rgb(35, 99, 19);
}

.swiper-slide:nth-child(8n) {
    background-color: rgb(0, 68, 255);
}

.swiper-slide:nth-child(9n) {
    background-color: rgb(218, 12, 218);
}

.swiper-slide:nth-child(10n) {
    background-color: rgb(54, 94, 77);
}
